.countdown {
    display: flex;
    align-items: center;
    width: 100%;
    font-size: 16px;
    text-align: center;
  }
  
  .countdown__num {
    width: 100%;
  }
  
  .countdown__num span {
    font-size: 3em;
    font-weight: bold;
  }
  
  .countdown__label {
    font-size: inherit;
    text-transform: uppercase;
  }
